import React from 'react'
import Emoji from 'react-emoji-render'
import styled from 'react-emotion'
import Helmet from 'react-helmet'
import Avatar, { StyledAvatar } from '../components/Avatar'
import BanquetForAll from '../components/BanquetForAll'
import Footer from '../components/Footer'
import Link from '../components/Link'
import Navbar from '../components/Navbar'
import { colors } from '../theme'

const stepHeight = 200

const StyledOneTwoThree = styled.section`
  h1 {
    text-align: center;
    font-size: 2.3rem;
    font-weight: 900;
    color: ${colors.text.normal};
    max-width: 722px;
    margin: 0 auto;
    padding: 0 20px;
    padding-top: 150px;
  }
  h2 {
    color: ${colors.text.light3};
    font-size: 1.5rem;
    font-weight: normal;
    max-width: 655px;
    text-align: center;
    margin: 0 auto;
    margin-top: 30px;
    padding: 0 20px;
  }
  h3 {
    color: ${colors.text.normal};
    text-align: center;
    max-width: 722px;
    margin: 0 auto;
    font-weight: 900;
    font-size: 2.3rem;
    padding: 0 20px;
  }
  .timeline {
    max-width: 655px;
    margin: 0 auto;
    margin-top: 60px;
    display: flex;
  }

  .line {
    margin-right: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .dot {
    position: relative;
    color: ${colors.text.normal};
    text-align: center;
    border: 4px solid ${colors.brand.normal};
    border-radius: 50px;
    font-size: 1.8rem;
    font-weight: 900;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: ${stepHeight + 10}px;

    &.final {
      font-size: 3rem;
      height: 80px;
      width: 80px;
      margin-bottom: 0;
    }

    &:not(:last-child)::after {
      content: ' ';
      position: absolute;
      width: 4px;
      height: ${stepHeight}px;
      background: ${colors.brand.normal};
      top: 50px;
      border-radius: 50px;
    }
  }

  .step {
    height: ${stepHeight + 54}px;
    margin-top: 7px;

    .num {
      display: none;
      font-weight: 900;
      font-size: 2rem;
      position: relative;

      &::after,
      &::before {
        content: ' ';
        width: 20px;
        border-radius: 50px;
        height: 5px;
        display: inline-block;
        background: ${colors.brand.normal};
        margin: 20px;
        margin-bottom: 7px;
        top: 21px;
      }
    }

    &.final {
      height: auto;
      margin-top: 17px;
    }

    .title {
      font-size: 1.5rem;
      font-weight: 600;
      color: ${colors.text.normal};
    }
    .content {
      font-size: 1.4rem;
      color: ${colors.text.normal};
      margin-bottom: 10px;
      margin-top: 20px;
    }
    .link {
      color: ${colors.secondary.light};
      font-size: 1.4rem;
    }
  }

  .flow {
    max-width: 600px;
    margin: 0 auto;
    margin-top: 60px;
    font-size: 1.4rem;
    color: ${colors.text.normal};
    padding: 0 20px;

    p:not(:last-child) {
      margin-bottom: 40px;
    }

    .link {
      color: ${colors.secondary.light};
      font-size: 1.4rem;
    }
  }
  ${StyledAvatar} {
    margin-bottom: -10px;
  }

  @media (max-width: 768px) {
    h1 {
      font-size: 2rem;
      padding-top: 100px;
    }
    .line {
      display: none;
    }
    .steps {
      padding: 0 20px;

      .step {
        height: auto;
        margin-bottom: 40px;
        text-align: center;
      }
      .step.final {
        margin-bottom: 0;
      }

      .num {
        display: block;
      }
    }
    .dot {
      width: 45px;
      height: 45px;
    }
  }
`

const OneTwoThree = () => (
  <StyledOneTwoThree>
    <Helmet>
      <title>
        Comment ça marche ? Un traiteur pour tous vos moments • Monbanquet.fr
      </title>
      <meta
        name="description"
        content="Le secret du meilleur service traiteur à Paris et à Lyon : des produits délicieux, un équipe reactif et l'experience de plus de 3000 moments en entreprise !"
      />
    </Helmet>
    <Navbar />
    <h1>
      <Emoji text="UN, DEUX, TROIS : PARTY 🎉" />
    </h1>
    <h2>
      Notre métier est de créer des moments de convivialité et ça commence dès
      la commande de votre banquet
    </h2>
    <div className="timeline">
      <div className="line">
        <div className="dot">1</div>
        <div className="dot">2</div>
        <div className="dot">3</div>
        <div className="dot final">
          <Emoji text="🎊" />
        </div>
      </div>
      <div className="steps">
        <div className="step">
          <span className="num">1</span>
          <div className="title">
            <Emoji text="Café ☕️, bière 🍻 ou champagne 🥂 ?" />
          </div>
          <div className="content">
            Un juste choix de produits, en bonne quantité, une touche de service
            et un brin de décoration. Nous avons tous les ingrédients pour
            réussir un événement sans contrainte !
          </div>
          <Link to="/commande" className="link">
            Passez commande →
          </Link>
        </div>
        <div className="step">
          <span className="num">2</span>
          <div className="title">
            <Emoji text="Invitez qui vous voulez : on s’occupe du reste 🧙" />
          </div>
          <div className="content">
            Nos meilleurs artisans locaux se mobilisent le jour J et nous
            coordonnons toute la logistique en vous tenant informé. Fini le
            stress de début d'événement !
          </div>
          <Link to="/nos-artisans" className="link">
            En savoir plus →
          </Link>
        </div>
        <div className="step">
          <span className="num">3</span>
          <div className="title">
            <Emoji text="Soyez prêts à déguster ⏱" />
          </div>
          <div className="content">
            Votre banquet est livré sur plateaux une heure avant votre événement
            : rien à réchauffer, rien à découper, juste à déguster ! Nos maîtres
            d'hôtel vous accompagnent sur vos plus beaux événements.
          </div>
        </div>
        <div className="step final">
          <span className="num">
            <Emoji text="🎊" />
          </span>
          <div className="title">
            <Emoji text="Profitez !" />
          </div>
        </div>
      </div>
    </div>
    <BanquetForAll />
    <h3>Une touche de logistique, un gros travail d’équipe</h3>
    <div className="flow">
      <p>
        Lorsque vous passez commande, Louise, Danielle ou Jean-Charles préparent
        votre événement en s’adaptant à vos contraintes de temps, de budget et…
        de goût. Paul et Martin auront avant sélectionné{' '}
        <Link to="/nos-artisans" className="link">
          les meilleurs artisans locaux
        </Link>.
      </p>
      <p>
        La conférence est au 10ème étage, le parking est inaccessible, vous
        n'avez pas le code d'entrée... C’est compliqué pour vous : c’est notre
        quotidien !
      </p>
      <p>
        Laëtitia s’assure que tout se passe comme sur des roulettes et vous
        tient au jus. Claire fait en sorte que vos livraisons arrivent au bon
        endroit et au bon moment. Gaëtan, Laura et Pierre coordonnent le service
        sur place de manière à ce que Ludovic , Alessandro ou Olivia vous
        laissent profiter de vos invités en toute sérénité.
      </p>
    </div>
    <BanquetForAll>
      Voyez ce que donne l’expérience de 3203 banquets réussis en nous voyant à
      l’oeuvre lors de votre prochain événement professionnel
    </BanquetForAll>
    <Footer />
  </StyledOneTwoThree>
)

export default OneTwoThree
export { StyledOneTwoThree }



